import React from 'react';
import ReactDOM from 'react-dom'
import {withRouter} from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CardActionArea from '@material-ui/core/CardActionArea'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import Paper from '@material-ui/core/Paper'
import Hidden from '@material-ui/core/Hidden'
import Drawer from '@material-ui/core/Drawer';
import Divider from '@material-ui/core/Divider';

import ScrollspyNav from "react-scrollspy-nav";

import '../App.css';

import {large_threshold} from '../files/constants.js'

//Image imports
import aneurysm_img from '../images/aneurysm.png'
import aneurysm_flow_img from '../images/aneurysm_flow.png'
import location_img from '../images/aneurysm_location.png'
import coiling_img from '../images/coiling.svg'
import donothing_img from '../images/donothing.png'
import angio_img from '../images/angio.jpg'

import pron_file from '../files/ca_pronounced.mp3'

import VolumeUpIcon from '@material-ui/icons/VolumeUp'
import ChevronRight from '@material-ui/icons/ChevronRight'
import ChevronLeft from '@material-ui/icons/ChevronLeft'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import Close from '@material-ui/icons/Close'

//
var pron = new Audio(pron_file)

class Landing extends React.Component {

  constructor(props) {
    super(props);
    this.state = {

      isLarge: window.innerWidth > large_threshold,
      risk_rupture: typeof this.props.match.params.risk !== 'undefined'
        ? this.props.match.params.risk
        : '',
      risk_endo: typeof this.props.match.params.risk_endo !== 'undefined'
        ? this.props.match.params.risk
        : '',
      risk_cran: typeof this.props.match.params.risk_cran !== 'undefined'
        ? this.props.match.params.risk
        : '',
      location: null,
      drawer: false
    }
  }

  componentWillMount() {
    window.addEventListener('resize', () => {
      this.setState({
        isLarge: window.innerWidth > large_threshold,
        risk: typeof this.props.match.params.risk !== 'undefined'
          ? this.props.match.params.risk
          : ''
      });
    }, false);
    if (/^\d+$/.test(this.state.risk_rupture)) {
      this.setState({
        risk: this.state.risk_rupture + '%'
      })
    }
  }
  componentDidUpdate() {
    var hash = this.props.location.hash.replace('#', '');
    if (hash) {
      let node = ReactDOM.findDOMNode(this.refs[hash]);
      if (node) {
        node.scrollIntoView()

        window.scrollBy(0, -100);
      }
    }
  }

  pronounce() {

    pron.play();
  }

  locationStatement() {
    var div = null;
    if (this.state.location != null && this.state.location.length > 0) {
      div = " Your aneurysm is located on the " + this.state.location + ".";
    }

    return div;
  }

  toggleDrawer() {
    this.setState({
      drawer: !this.state.drawer
    });
  };

  drawer() {
    return (<div className={'landing-nav norm_padding'}>
      <Typography variant={'h6'}>
        Quick Navigation:</Typography>
      <div className={'landing-nav'}>
        <ScrollspyNav scrollTargetIds={["pathology", "location", "risk", "options"]} activeNavClass="is-active" router={'Router'}>
          <a href="#pathology">What is an aneurysm?</a>
          <a href="#location">Where is my aneurysm?</a>
          <a href="#risk">Am I at risk?</a>
          <a href="#decisionframe">Shoudl I do something?</a>
          <a href="#options">What are the treatment options?</a>
        </ScrollspyNav>
      </div>
    </div>)
  }

  render() {
    var size_class = this.state.isLarge
      ? 'large'
      : ''
    return (<div>

      <Grid container={true} direction={'row'} alignContent={'stretch'}>
        <Grid container={true} item={true} justify={'flex-start'} alignContent={'flex-start'} className={'first-block'} md={8} xs={12}>
          <Paper>
            <Grid item={true} container={true} justify={'start'} xs={12}>
              <Typography variant='h3' className={'landing-title'}>I have an aneurysm.</Typography>
            </Grid>
            <Grid item={true} containera={true} justify={'start'} xs={12}>
              <Typography variant='h4' gutterBottom={true} className={'landing-subtitle'}>What do I do now?</Typography>
            </Grid>
            <Grid item={true} xs={12}>
              <Typography variant='body2' gutterBottom={true} className={'limit_md landing-welcome'}>Being diagnosed with an aneurysm can be overwhelming. This guide has the essential information to help you understand aneurysms and how they are treated.</Typography>
            </Grid>
            <Divider/>
            <Grid className={'content-block summary'} item={true} xs={12} container={true} direction={this.state.isLarge
                ? 'row-reverse'
                : 'column'}>

              <Grid item={true} xs={12} sm={6}>
                <img src={aneurysm_img} className={'grid_img'}/>
              </Grid>
              <Grid item={true} xs={12} sm={6}>

                <div className={size_class + ' summary-block-text'}>

                  <p className={'supertitle'}>BASICS:</p>
                  <Typography variant='title' gutterBottom={true}>Need to know: aneurysms</Typography>
                  <Typography variant='body1' paragraph={true}>Cerebral aneurysms
                    <VolumeUpIcon className={'inline-icon'} color={'primary'} onClick={() => this.pronounce()}/>
                    are a problem with blood vessels in the brain. For some people, they can cause problems, but for most people, they will not.
                  </Typography>
                  <Typography variant='body1' paragraph={true}>2 kinds of problems caused by aneurysms:
                    <ul>
                      <li>they can cause headaches and other issues like blurred vision, weakness, numbness, or difficulty speaking by growing big and pressing on the brain.</li>
                      <li>they can
                        <b>rupture</b>, causing a bleed in the brain that can be deadly or cause permanent brain damage.</li>
                    </ul>
                  </Typography>
                  <Typography variant='body1' paragraph={true}>
                    <b>Aneurysms are treatable</b>, and your provider will work with you to determine the best plan for you.
                  </Typography>

                </div>
              </Grid>
            </Grid>
            <Divider/>
            <Grid id={'pathology'} item={true} container={true} xs={12} alignItems={'center'} className={'content-block '} direction={'row'}>
              <Grid item={true} xs={12} sm={6} className={'norm_padding'}>
                <img src={aneurysm_flow_img} className={'limit_md grid_img'}/>
              </Grid>
              <Grid item={true} xs={12} sm={6} className='landing_text_block'>

                <Typography variant='title' gutterBottom={true}>What is an aneurysm?</Typography>
                <Typography variant={'body1'} paragraph={true}>An aneurysm is an abnormal growth from the wall of the blood vessels in the brain. About 3% of people have an aneurysm. Most will never have a problem caused by their anuerysm.
                </Typography>

                <Typography variant='title' gutterBottom={true}>How do aneurysms cause problems?</Typography>
                <Typography variant={'body1'} paragraph={true}>Some anerysms will grow large and press on the brain causing headaches and other neurological issues that people may experience. The wall of the aneurysm may also be weak compared the wall of normal blood vessels. For this reason, they can rupture and leak blood into the brain.
                </Typography>
              </Grid>
            </Grid>
            <Divider/>
            <Grid id={'location'} item={true} container={true} xs={12} alignItems={'center'} className={'content-block '} direction={'row'}>

              <Grid item={true} xs={12} sm={6} className='landing_text_block'>

                <Typography variant='title' gutterBottom={true}>Where are aneurysms located?</Typography>
                <Typography variant={'body1'} paragraph={true}>Most aneurysms are located on the arteries at the base of the brain. Aneurysm location is important because it can predict how likely your aneurysm is to rupture and determine what kind of treatment is best for you.
                  <b>{this.locationStatement()}</b>
                </Typography>

              </Grid>
              <Grid item={true} xs={12} sm={6} className={'norm_padding'}>
                <img src={location_img} className={'limit_md grid_img'}/>
              </Grid>
            </Grid>
            <Divider/>
            <Grid id={'risk'} item={true} container={true} xs={12} className={'content-block '} alignItems={'center'}>
              <Grid item={true} xs={12} sm={8} className='landing_text_block'>
                {
                  this.state.risk_rupture !== ''
                    ? <div>
                        <Typography variant={'h6'} justify={'center'}>My Risk of Rupture in Next 5 Years:</Typography>
                        <Typography variant={'h3'} justify={'center'} color={'primary'} className={'big-number'}>{this.state.risk_rupture.replace('_', ' ')}</Typography>
                        <Typography variant={'caption'}>this number is an
                          <b>estimate</b>
                          based on age, ethnicity, aneurysm size, aneurysm location, presence of hypertension, and history of subarachnoid hemorrhage. Your actual risk of rupture may be higher or lower based on other parts of your health history or charaterystics of your aneurysm.</Typography>

                      </div>

                    : <div>
                        <Typography variant={'h6'} gutterBottom={true}>What is my risk of rupture?</Typography>
                        <Typography variant={'body1'} gutterBottom={true}>Your doctor will review your records to determine what your risk of rupture might be based on your medical history and the appearance of your aneurysm.</Typography>
                        <Button href="#/data-entry">Risk Calculator</Button>
                      </div>
                }
              </Grid>
            </Grid>
                        <Divider/>
            <Grid id={'decisionframe'} item={true} container={true} xs={12} className={'content-block'} alignItems={'center'}>
              <Grid item={true} xs={12} sm={8} className='landing_text_block'>
                <p className={'supertitle'}>Treatment:</p>
                <Typography variant={'title'} gutterBottom={true}>Should I do something about it?</Typography>
                <Typography variant={'body1'} paragraph={true}>Studies have been done that help doctors predict which aneurysms may cause problems. Although it might seem like a good idea to treat all aneurysms just in case, the treatments themselves can cause problems, or complications. For this reason, the risk and benefits of treatment must be balanced, and this balance will be different for every person.
                </Typography>
                <Typography variant={'body1'} paragraph={true}>
                  When you meet the doctor who will manage your aneurysm, you will discuss the risks and benefits of treating or not treating your aneurysm.
                </Typography>
              </Grid>
            </Grid>
                        <Divider/>
            <Grid id={'options'} item={true} container={true} className={'content-block  '}>
              <Grid item={true} xs={12}>
                <p className={'supertitle'}>TREATMENT:</p>
                <Typography variant={'title'} gutterBottom={true}>
                  What are my options?
                </Typography>
                <Typography variant={'body1'} className={'limit_lg'} gutterBottom={true} paragraph={true}>The best option for you will depend on how likely it is that your aneurysm will rupture balanced against your individual needs and the risks of surgery.</Typography>
              </Grid>
              <Grid item={true} container={true} spacing={24}>
                <Grid item={true} xs={12} sm={4} md={4} container={true}>
                  <Card>
                    <CardActionArea href={'#/about-rfm'}>
                      <CardMedia component="img" image={donothing_img} title="Do Nothing"/>
                      <CardContent>
                        <Typography gutterBottom={true} variant="h5" component="h2">
                          Risk Reduction
                        </Typography>
                        <Typography gutterBottom={true} variant="subtitle2" component="h2">
                          Goal: reduce risk of rupture
                        </Typography>
                        <Typography component="p">
                          Some aneurysms are so unlikely to rupture that testing or intervention is more likely to cause harm. For these, it is best to do nothing at all beyond living healthfully, including avoiding smoking and ensuring blood pressure and cholesterol are under control.
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                    <CardActions>
                      <Button href={'#/about-rfm'} size={'small'} style={{
                          'margin-left' : 'auto'
                        }}>Learn More</Button>
                    </CardActions>
                  </Card>

                </Grid>
                <Grid item={true} xs={12} sm={4} md={4} container={true}>
                  <Card>
                    <CardActionArea href={'#/about-img'}>
                      <CardMedia component="img" image={angio_img} title="Watchful Waiting"/>
                      <CardContent>
                        <Typography gutterBottom={true} variant="h5" component="h2">
                          Watch and Wait
                        </Typography>
                        <Typography gutterBottom={true} variant="subtitle2" component="h2">
                          Goal: collect more data
                        </Typography>
                        <Typography component="p">
                          For some aneurysms with a low risk of rupture, it is best not to perform a risky procedure. However, it can be helpful to wait some time and then test again to see whether the anuerysm is growing - a sign that the anuerysm is more likely to rupture.

                        </Typography>
                      </CardContent>
                    </CardActionArea>
                    <CardActions>
                      <Button size={'small'} style={{
                          'margin-left' : 'auto'
                        }} href={'#/about-img'}>Learn More</Button>
                    </CardActions>

                  </Card>

                </Grid>
                <Grid item={true} xs={12} sm={4} container={true}>
                  <Card>
                    <CardActionArea href={'#/about-surg'}>
                      <CardMedia component="img" image={coiling_img} title="Surgical Intervention"/>
                      <CardContent>
                        <Typography gutterBottom={true} variant="h5" component="h2">
                          Have Surgery
                        </Typography>
                        <Typography gutterBottom={true} variant="subtitle2" component="h2">
                          Goal: decrease rupture risk
                        </Typography>
                        <Typography component="p">
                          For anuerysms that are more likely to rupture, it may be worth the risk to do surgery to treat the aneurysm. The intent of these surgeries is to reduce the risk of rupture in the future.
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                    <CardActions style={{
                        'margin-top' : 'auto'
                      }}>
                      <Button size={'small'} style={{
                          'margin-left' : 'auto'
                        }} href={'#/about-surg'}>Learn More</Button>
                    </CardActions>
                  </Card>
                </Grid>
                <Grid item={true} container={true} xs={12} justify={'flex-end'}>
                  <Button href='#/options' color={'primary'} size={'large'} variant={'contained'}>See Comparison<ChevronRight/></Button>
                </Grid>
              </Grid>

            </Grid>
          </Paper>
        </Grid>
        <Hidden smDown={true}>
          <Grid item={true} sm={4}>
            {this.drawer()}
          </Grid>
        </Hidden>
        <Drawer anchor={'right'} open={this.state.drawer} onClose={() => this.toggleDrawer()}>
          <div tabIndex={0} role="button" onClick={() => this.toggleDrawer()} onKeyDown={() => this.toggleDrawer()}>
            <IconButton onClick={()=>this.toggleDrawer()}><Close/></IconButton>
            {this.drawer()}
          </div>
        </Drawer>

      </Grid>
      <Hidden mdUp={true}>
        <IconButton onClick={() => this.toggleDrawer()} className={'drawer-button'}><ChevronLeft/></IconButton>
      </Hidden>
    </div>)
  }
}

export default Landing;
